import React from 'react';
import { Grid } from '@mui/material';
import Weeks from './Weeks';

// component of weather focast
function Weather ({today, weeks, weeks_pressure}){
    return (
        <div className='weather'>
          <Grid container spacing={0}className="result1">
            <Grid item xs={3.99} md={3.99} className="result1_item">
              <h2>{today.city}, {today.country}</h2>
              <h4>{today.date}</h4>
            </Grid>
            <Grid item xs={3.99} md={3.99} className="result1_item" style={{paddingTop: "0.4rem" }}>
              <img
                src={`https://openweathermap.org/img/w/${today.icon}.png`}
                alt={today.icon}
                />
              <h4 style={{marginTop: 0}}>{today.lTemp} °C / {today.hTemp} °C</h4>
            </Grid>
            <Grid item xs={3.99} md={3.99} className="result1_item">
              <h2>{today.pressure} hPa</h2>
              <h4>{today.temp} °C</h4>
            </Grid>
          </Grid>

          <Grid container spacing={0}>
            {weeks.map((data) => (
              <Grid item xs={1.71} md={1.71} key={data.key} className="result2">
                { (data.key === 0) ? 
                    <h5>Today</h5>
                  :
                    <h5>{data.day}</h5>
                }
                <img
                  src={`https://openweathermap.org/img/w/${data.icon}.png`}
                  alt={data.icon}
                />
                <p className='weekTemp'>{data.pressure} hPa</p>
                <p className='weekTemp'>{data.lTemp} °C / {data.hTemp} °C</p>
              </Grid>
              ))
            }
          </Grid>
          <Weeks data = {weeks_pressure}/>
        </div>
    )
}

export default Weather