import React, { useState } from 'react';
import axios from 'axios';
import './App.css';
import Weather from './components/Weather';
import Find from './components/Find';
import Loading from './components/Loading'
import API_Weather from './Api';

function App() {
  const [state, setState] = useState({
    value: '',
    current: {},
    hourly: {},
    weeks: [],
    weeks_pressure:[],
    loading: false,
    error: false,
  })
  
  const handleLocationChange = e => {
    setState({
      ...state,
      value: e.target.value,
    })
  };

  const handleLocationSubmit = e => {
    e.preventDefault();
    setState({
      ...state,
      loading: true,
    })
    axios.get(API_Weather(state.value))
    .then(res =>{
      console.log(res)
      const data = res.data

      const newDate = new Date();
      const month = ["Jan", "Feb", "May", "Apr", "Mar", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec" ]
      const date = newDate.getFullYear() + " " + month[(newDate.getMonth())] + " " + newDate.getDate();

      const current = {
        city: data.city.name,
        country: data.city.country,
        date,
        population: data.city.population,
        desc: data.list[0].weather[0].description,
        main: data.list[0].weather[0].main,
        icon: data.list[0].weather[0].icon,
        temp: Math.round(data.list[0].temp.day),
        hTemp: Math.round(data.list[0].temp.max),
        lTemp: Math.round(data.list[0].temp.min),
        clouds: data.list[0].clouds,
        pressure: data.list[0].pressure,
      }

      const hourly = {
        city: data.city.name,
        country: data.city.country,
        date,
        population: data.city.population,
        desc: data.list[0].weather[0].description,
        main: data.list[0].weather[0].main,
        icon: data.list[0].weather[0].icon,
        temp: data.list[0].temp.day,
        hTemp: data.list[0].temp.max,
        lTemp: data.list[0].temp.min,
        clouds: data.list[0].clouds,
        humidity: data.list[0].humidity,
        wind: data.list[0].speed,
        pressure: data.list[0].pressure,
      }

      const weeks_Data = data.list
      const weeks = weeks_Data.map((data, index) => {
        return{
          key: index,
          main: data.weather[0].main,
          // temp: data.temp.day,
          date,
          day: new Date(data.dt * 1000).toLocaleString('en-US', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' }).slice(0,3),
          dt: data.dt,
          desc: data.weather[0].description,
          icon: data.weather[0].icon,
          hTemp: Math.round(data.temp.max),
          lTemp: Math.round(data.temp.min),
          pressure: data.pressure,
        }})

        const weeks_pressure = weeks.map((data, index) => {
          return{
            x:new Date(data.dt * 1000).toLocaleString('en-US', { weekday: 'short', year: 'numeric', month: 'long', day: 'numeric' }).slice(0,3),
            y:data.pressure,
          }})

        setState({
          ...state,
          current,
          hourly,
          weeks,
          weeks_pressure,
          loading:false,
          error: false
        })
      
    })
    .catch(error => {
      console.log(error);

      setState({
        ...state,
        current:{},
        hourly:{},
        weeks:[],
        weeks_pressure:[],
        loading:false,
        error: true
      })
    
    });
    }

  return (
    <div>
      <Find 
        value={state.value} 
        data={state}
        change={handleLocationChange}
        submit={handleLocationSubmit}
        // showResult={(state.weatherInfo || state.error) && true}
      />
      {state.loading === true ? <Loading /> : 
      <div>
        {state.current.country !== undefined ? 
          <div className="weather">
            <Weather today={state.current} hourly={state.hourly} weeks={state.weeks} weeks_pressure={state.weeks_pressure}/>
          </div> : 
        state.error ? 
          <p className="error__loc">There aren't any information on that location.</p> :
          <div></div>
        }
      </div>
      }  
    </div>
  );
}

export default App;
